import { ApiService } from '@tableau/api-shared-js/src/ApiShared/Services/ServiceRegistry';

/**
 * Service for providing auth tokens to the viz.
 *
 * @interface AuthService
 * @extends {ApiService}
 */
export interface AuthService extends ApiService {
  /**
   * Passes an auth token to the Viz.
   * Please note that this call does not go through our Command infrastructure and is an exception.
   *
   * @param token
   */
  setAuthToken(token: string): void;
}
